body {
    background-color: #42adf5;
    height: 100vh;
    color: white;
    text-align: center;
    font-size: 1.5em;
    margin-top: -20;

}

input[type='checkbox'] {
    margin: 0 1em;
}

.header h1 {
    margin: 0 auto;
}

.header-text {
    font-size: 0.8em;
}

.todo-item {
    margin: 1em auto;
    background: rgb(36, 36, 43);
    width: 60vw;
    text-align: left;
    padding: 1em;
    box-shadow: 0 8px 6px -6px black;

}

input[type="checkbox"] {
    width: 0.65em;
    height: 0.65em;
    cursor: pointer;

}

.task-state {
    float: right;
}

.form {
    margin: 0 auto;
    width: 24.7em;
    height: 1.93em;
    box-shadow: 0 8px 6px -6px black;

}

input[type=text] {
    width: 19.4em;
    height: 1.33em;
    float: left;
    border: none;
    text-align: center;
    padding: 0;
    margin: 0;
    border: 0.3em solid rgb(42, 42, 48);
    border-right: none;
    background: rgb(75, 80, 87);
    color: white;
}

input:focus {
    outline: none;
    outline-offset: 0;
}

.submit {
    width: 5em;
    height: 1.93em;
    padding: 0;
    margin: 0;
    float: right;
    border: none;
    color: white;
    background: rgb(42, 42, 48);
}

.submit:focus {
    outline: none;
    outline-offset: 0;
}

.submit:hover {
    background: rgb(50, 50, 56);
}




.delete {
    width: 1.5em;
    height: 1.5em;
    float: right;
    border: none;
    background: none;
    cursor: pointer;
    color: white;
    
}

.delete:hover {
    background: rgb(224, 59, 59);
    border-radius: 5px;

}

@media only screen and (max-width: 411px) {
    body {
        width: 100%;
        height: 100%;
    }

    .form {
        margin: 0 auto;
        width: 95vw;
        height: 7vh;
        box-shadow: 0 8px 6px -6px black;

    }

    input[type=text] {
        width: 65vw;
        height: 4.45vh;
        float: left;
        text-align: left;
        padding: 0;
        margin: 0;
        border: 0.3em solid rgb(42, 42, 48);
        border-right: none;
        background: rgb(75, 80, 87);
        color: white;
    }



    .submit {
        width: 27.75vw;
        height: 6.9vh;
        padding: 0;
        margin: 0;
        float: right;
        color: white;
        background: rgb(42, 42, 48);
    }



    .todo-item {
        margin: 1em auto;
        padding: 0.5em;
        width: 85vw;
        height: 7vh;

    }
    .delete{
        position: absolute;
        right: 20px;
    }

    input[type="checkbox"] {
        width: 0.6em;
        height: 0.6em;
        margin-left: 0.2em;
        margin-right: 0.5em;
        cursor: pointer;

    }

}